:root {
  --filter-input-offset: 15px;
  --tag-color: #6ad4b0;
  --filter-width: 265px;

  --mq-mobile: 576px;
  --mq-desktop: 577px;
}

html {
  font-size: 14px;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 14px;
  line-height: 1.4285em;
}

*, :after, :before {
  box-sizing: border-box;
}

button, select {
  text-transform: none;
}

button, input {
  overflow: visible;
}

button, input, optgroup, select, textarea {
  font-family: sans-serif;
  font-size: 100%;
  line-height: 1.15;
  margin: 0;
}

h3 {
  font-size: 1.28571429rem;
}

h1, h2, h3, h4, h5 {
  font-family: Lato,'Helvetica Neue',Arial,Helvetica,sans-serif;
  line-height: 1.28571429em;
  margin: calc(2rem - .14285714em) 0 1rem;
  font-weight: 700;
  padding: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
  font-family: "Lato", sans-serif;
  overflow-x: hidden;
}

main {
  display: flex;
}

.sidebar {
  display: flex;
  flex-direction: column;
}

.app {
  padding-bottom: 50px;
}

.main-header {
  padding: 30px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  display: flex;
  color: #a1a1a1;
  justify-content: space-between;
  margin: 30px 30px;
}

.main-header h1 {
  font-size: 48px;
}

.social {
  margin-top: 10px;
}

footer {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  text-align: center;
  background: #65d6af;
  padding: 10px 0;
  z-index: 100;
}

footer a,
footer a:hover {
  padding: 0 3px;
  color: #fff;
  text-decoration: underline;
}

@media all and (max-width: 576px) {
  .logo {
    display: flex;
    justify-content: center;
  }
}

.mentors-wrapper {
  padding-bottom: 30px;
}

@media all and (max-width: 576px) {
  main {
    flex-direction: column;
  }
  .mentors-wrapper {
    position: relative;
    background: #fff;
    -webkit-transform: translateY(0);
            transform: translateY(0);
    transition: -webkit-transform 0.3s ease;
    transition: transform 0.3s ease;
    transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  }

  .mentors-wrapper.active {
    -webkit-transform: translateY(190px);
            transform: translateY(190px);
    margin-bottom: 50px;
  }
}

.mentors-wrapper > .mentors-cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  overflow: auto;
  margin-top: 30px;
}

.nothing-to-show {
  font-size: 20px;
  line-height: 1.6;
}

.card {
  --avatar-offset: 54px;
  --avatar-dim: 108px;

  display: flex;
  flex-direction: column;
  width: 280px;
  padding: 0 32px;
  border-radius: 2px;
  text-align: center;
  position: relative;
  border: 1px solid #ccc;
  margin-top: calc(var(--avatar-offset) + 10px);
  margin-right: 5px;
  margin-left: 5px;
}

.card .country {
  position: absolute;
  top: 10px;
  left: 10px;
}

.card .avatar {
  width: var(--avatar-dim);
  height: 118px;
  margin: calc(var(--avatar-offset) * -1) auto 0;
}

.card .avatar i {
  position: absolute;
  font-size: var(--avatar-dim);
  width: var(--avatar-dim);
  height: var(--avatar-dim);
  background: #fff;
  color: rgb(155, 155, 155);
  border-radius: 50%;
  box-shadow: 0 0 10px 0;
  display: block;
}

.card .avatar img {
  display: block;
  position: relative;
  border-radius: 50%;
  width: var(--avatar-dim);
  height: var(--avatar-dim);
}

.card .name {
  color: #4a4a4a;
  font-size: 20px;
  margin: 0 0 5px;
}

.card .title {
  color: #999;
  margin: 0 0 5px;
}

.card .description {
  font-style: italic;
  margin-bottom: 5px;
}

.card .tags {
  margin-bottom: 10px;
  margin: 10px -10px;
}

.card .tag {
  color: #fff;
  background: var(--tag-color);
  border-radius: 20px;
  display: inline-block;
  padding: 5px 10px;
  margin: 0 2.5px 5px;
  line-height: 1;
  text-transform: lowercase;
}

.card .channels {
  display: flex;
  flex-direction: column;
  flex-grow: 1;
  justify-content: flex-end;
}

.card .channels .channel-inner {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #ccc;
  align-items: center;
  padding: 10px;
}

.card .channels a {
  color: #9b9b9b;
}

.card .channels a .icon {
  transition: -webkit-transform 0.3s ease;
  transition: transform 0.3s ease;
  transition: transform 0.3s ease, -webkit-transform 0.3s ease;
  -webkit-transform-origin: 50%;
          transform-origin: 50%;
}

.card .channels a:hover .icon {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}

.card .channels a .type::first-letter {
  text-transform: uppercase;
}

.card .channels a {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.card .channels .type {
  margin: 5px 0px;
}

.channel-label {
  text-decoration: none;
}

.card .like-button {
  position: absolute;
  top: 10px;
  right: 10px;
  background: none;
  border: none;
  cursor: pointer;
}

.card .like-button .liked,
.card .like-button:hover i {
  color: #db2828;
}

#mentorCount {
  font-size: 0.65em;
  padding-left: 0.5em;
  font-weight: 400;
}

#filter {
  background-color: rgba(105, 213, 177, 0.3);
  color: #4a4a4a;
  font-size: 1.5em;
  text-transform: uppercase;
  padding: 15px 0px 15px 30px;
}

.toggle-filter {
  float: right;
  margin-right: 10px;
  background: none;
  border: none;
}

.toggle-filter i {
  transition: -webkit-transform 0.15s ease;
  transition: transform 0.15s ease;
  transition: transform 0.15s ease, -webkit-transform 0.15s ease;
}

.toggle-filter i.show-filters {
  -webkit-transform: rotateZ(90deg);
          transform: rotateZ(90deg);
}

@media all and (max-width: 576px) {
  .inputs {
    position: absolute;
    left: 0;
    width: 100%;
  }
  .filter-wrapper {
    order: 2;
  }
}

@media all and (min-width: 577px) {
  .toggle-filter {
    display: none;
  }
}

.ac .highlight {
  background: rgba(105, 213, 177, 0.3);
}

.ac .ac-menu {
  position: absolute;
  z-index: 1;
  left: var(--filter-input-offset);
  width: calc(100% - var(--filter-input-offset));
  max-height: 150px;
  overflow: auto;
  background: #fff;
  border: 1px solid rgba(155, 155, 155, 0.5);
  border-top: 0;
}

@media all and (max-width: 576px) {
  .ac .ac-menu {
    width: calc(100% - var(--filter-input-offset) * 2);
  }
}

.ac .ac-item {
  cursor: pointer;
  padding: 5px;
}
.label {
  position: absolute;
  top: -10px;
  left: 25px;
  background-color: #fff;
  text-transform: uppercase;
  color: #4a4a4a;
  font-weight: bold;
}

.input-container {
  position: relative;
  margin-top: 20px;
}

.input {
  width: 250px;
  height: 40px;
  background: none;
  border: none;
  border: 1px solid rgba(155, 155, 155, 0.5);
  border-radius: 2px;
  margin-left: var(--filter-input-offset);
  padding-left: 10px;
  color: var(--tag-color);
}

@media all and (max-width: 576px) {
  .input {
    width: calc(100% - var(--filter-input-offset) * 2);
  }
}
.social-link {
  margin: 0px 10px;
}

.social-wrapper {
  align-self: center;
  margin-top: 30px;
}

@media all and (max-width: 576px) {
  .social-link {
    margin: 0 5px;
  }
}

